body {
  color: #333;
  background-color: #f4f4f4;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 20px;
  font-family: Arial, Helvetica Neue, sans-serif;
  display: flex;
}

h1 {
  text-align: center;
  color: #333;
  margin-bottom: 10px;
}

.description {
  text-align: center;
  color: #666;
  max-width: 800px;
  margin-bottom: 20px;
  line-height: 1.4;
}

.parameters-container {
  background-color: #fff;
  border-radius: 12px;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  max-width: 800px;
  margin-bottom: 30px;
  padding: 25px;
  display: flex;
  box-shadow: 0 4px 12px #0000000d;
}

.parameters-grid {
  grid-template-columns: repeat(2, 1fr);
  gap: 25px;
  display: grid;
}

.parameter-section {
  background-color: #f8f9fa;
  border-radius: 8px;
  padding: 15px;
  transition: background-color .2s;
}

.parameter-section:hover {
  background-color: #f0f2f5;
}

.parameter-section h3 {
  color: #2c3e50;
  margin: 0 0 12px;
  font-size: 1em;
  font-weight: 600;
}

.parameter-row {
  align-items: center;
  gap: 12px;
  margin-bottom: 8px;
  display: flex;
}

.parameter-group {
  flex: 1;
  align-items: center;
  gap: 6px;
  display: flex;
}

.parameter-group label {
  color: #555;
  text-align: right;
  min-width: 35px;
  font-size: .9em;
}

.input-wrapper {
  flex: 1;
  align-items: center;
  gap: 4px;
  display: flex;
}

.input-wrapper span {
  color: #555;
  min-width: 15px;
  font-size: .9em;
}

.parameter-group input[type="number"] {
  color: #2c3e50;
  border: 1px solid #ddd;
  border-radius: 6px;
  width: 100%;
  min-width: 50px;
  padding: 8px 10px;
  font-size: 14px;
  transition: all .2s;
  box-shadow: inset 0 1px 3px #0000000d;
}

.parameter-group input[type="number"]:focus {
  border-color: #1976d2;
  outline: none;
  box-shadow: 0 0 0 3px #1976d21a;
}

.tooltip {
  color: #666;
  margin-top: 2px;
  font-size: .8em;
}

.primary-button {
  color: #fff;
  cursor: pointer;
  text-align: center;
  background-color: #2196f3;
  border: none;
  border-radius: 8px;
  align-self: center;
  min-width: 200px;
  margin: 10px 0;
  padding: 14px 28px;
  font-size: 15px;
  font-weight: 600;
  transition: all .2s;
  display: inline-block;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 4px #0000001a;
}

.primary-button:hover {
  background-color: #1976d2;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px #00000026;
}

.primary-button:active {
  transform: translateY(0);
  box-shadow: 0 2px 4px #0000001a;
}

.primary-button:disabled {
  cursor: not-allowed;
  background-color: #90caf9;
  transform: none;
}

.primary-button.loading {
  pointer-events: none;
  opacity: .9;
}

.primary-button.loading:after {
  content: "";
  border: 2px solid #ffffff4d;
  border-top-color: #fff;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  margin: -10px 0 0 -10px;
  animation: .8s linear infinite button-loading-spinner;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes button-loading-spinner {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(1turn);
  }
}

.main-container {
  background-color: #fff;
  border-radius: 12px;
  flex-direction: column;
  width: 90%;
  max-width: 800px;
  margin: 0 auto 40px;
  padding: 30px 20px;
  display: flex;
  box-shadow: 0 4px 12px #0000000d;
}

.simulation-layout {
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.left-panel {
  flex: 0 0 308px;
  justify-content: center;
  display: flex;
}

.cohort-grid {
  grid-template-rows: repeat(2, 150px);
  grid-template-columns: repeat(2, 150px);
  gap: 30px;
  width: 308px;
  padding-top: 36px;
  display: grid;
}

.grid-box {
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
}

.grid-title {
  text-align: center;
  color: #333;
  white-space: nowrap;
  width: 100%;
  font-size: .85em;
  font-weight: bold;
  position: absolute;
  top: -20px;
}

.grid {
  background-color: #fff;
  background-image: linear-gradient(#0000000d 1px, #0000 1px), linear-gradient(90deg, #0000000d 1px, #0000 1px);
  background-size: 10% 10%;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  width: 150px;
  height: 150px;
  transition: all .3s;
  position: relative;
  box-shadow: inset 0 0 30px #00000005;
}

.grid:hover {
  border-color: #d0d0d0;
  box-shadow: inset 0 0 35px #00000008;
}

.dot {
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
  transform: translate(-50%, -50%);
}

.comparison-dot {
  background-color: #2196f3b3;
  border: 1px solid #2196f3e6;
}

.target-dot {
  background-color: #f44336b3;
  border: 1px solid #f44336e6;
}

.legend {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  flex-direction: column;
  justify-content: center;
  width: 150px;
  height: 150px;
  padding: 10px;
  transition: all .3s;
  display: flex;
  position: relative;
  box-shadow: 0 4px 12px #00000008;
}

.legend:hover {
  border-color: #d0d0d0;
  box-shadow: 0 6px 16px #0000000d;
}

.legend-title {
  text-align: center;
  color: #2c3e50;
  white-space: nowrap;
  width: 100%;
  font-size: .85em;
  font-weight: 600;
  position: absolute;
  top: -20px;
  left: 0;
}

.legend-item {
  color: #34495e;
  align-items: center;
  margin-bottom: 8px;
  padding: 3px 0;
  font-size: .8em;
  transition: all .2s;
  display: flex;
}

.legend-item:hover {
  color: #2c3e50;
  transform: translateX(2px);
}

.legend-item:last-child {
  margin-bottom: 0;
}

.legend-dot {
  border-radius: 50%;
  width: 8px;
  height: 8px;
  margin-right: 8px;
  box-shadow: 0 2px 4px #0000001a;
}

.right-panel {
  flex: 0 0 308px;
  position: relative;
}

.right-panel:before {
  content: "Single Simulation Results";
  text-align: center;
  color: #333;
  white-space: nowrap;
  width: 100%;
  font-size: .85em;
  font-weight: bold;
  position: absolute;
  top: -24px;
}

.chart-container {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: 308px;
  height: 308px;
  margin: 24px 0 0;
  padding: 4px;
  display: flex;
  position: relative;
  box-shadow: 0 4px 12px #0000000d;
}

.multiple-sims-container .chart-container {
  width: 616px;
  height: 616px;
}

.chart-container canvas {
  aspect-ratio: 1 !important;
  width: 300px !important;
  max-width: 300px !important;
  height: 300px !important;
  max-height: 300px !important;
}

.multiple-sims-container .chart-container canvas {
  width: 600px !important;
  max-width: 600px !important;
  height: 600px !important;
  max-height: 600px !important;
}

.section-title {
  color: #2c3e50;
  text-align: center;
  margin: 40px 0 20px;
  font-size: 1.5em;
  font-weight: 600;
}

.multiple-sims-container {
  flex-direction: column;
  align-items: center;
  width: 90%;
  max-width: 800px;
  margin: 0 auto;
  display: flex;
}

.multiple-sims-container .description {
  text-align: center;
  color: #666;
  max-width: 600px;
  margin: 0 0 20px;
  font-size: .95em;
  line-height: 1.5;
}
